<template>
  <div>
    <mynav></mynav>
  </div>
</template>

<script>
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'

  export default {
    name: 'Home',
    components: {
      Mynav,
      MButton
    }
  }
</script>

<style scoped>
  .header-state {
    background: #468df1;
    height: 48px;
    color: rgba(255, 255, 255, .8);
    font-size: 16px;
    letter-spacing: 1px;
    text-align: center;
    line-height: 48px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.23);
  }

  .header-state span {
    cursor: pointer;
    display: inline-block;
    height: 45px;
    width: 180px;
  }

  .header-state span.active {
    color: #fff;
    border-bottom: 3px solid #fff;
  }

  .projects, .projects-public {
    margin-top: 24px;
    justify-content: center;
  }

  .card {
    background: #fff;
    border: 1px solid #d4d4d4;
    padding: 16px 20px;
    line-height: 40px;
    width: 202px;
    height: 211px;
    margin: 12px;
  }

  .card .title {
    color: #333;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 700;
  }

  .card .content {
    padding-top: 100px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.8);
  }

</style>


