<template>
  <div class="main" :style="{height: clientHeight + 'px'}">
    <div class="logo">
      <img src="../assets/logo.png">
    </div>
    <h3 class="center brand"></h3>
    <div class="login card">
      <input type="text" placeholder="" v-model="username" />
      <input type="password" placeholder="" v-model="password" @keyup.enter="login"/>
      <div class="save"><input type="checkbox" id="save"><label for="save"></label></div>
      <span class="btn center" @click="login"></span>
      <div class="forget center"><a href="forgetpassword"></a></div>
      <p class="signin center"><a href="regite"></a></p>
    </div>
  </div>

  <!--<div class="login" :style="{height: clientHeight + 'px'}">
    <h1>MedicaYun Patient Manage</h1>
    
    <div class="login-card">

      
      <h2 class="login-header"></h2>

      <form class="login-container">
        <p><input type="text" placeholder=""  v-model="username" ></p>
        <p><input type="password" placeholder="" v-model="password" @keyup.enter="login" ></p>
        <p><span class="submit" @click="login"></span></p>
      </form>
    </div>
  </div>-->
</template>

<script>
  import api from '../api.js'

  export default {
    data () {
      return {
        username: '',
        password: '',
        clientHeight: document.body.scrollHeight
      }
    },
    methods: {
      login () {
        if (!window.sessionStorage) { return }
        if (this.username === '' && this.password === '') { return }
        // session
        api.Login({username: this.username, password: this.password}).then(res => {
          window.sessionStorage.setItem('userid', res.userid)
          window.sessionStorage.setItem('authorization', res.token)
          window.sessionStorage.setItem('username', this.username)
          this.$root.userid = res.userid
          this.$router.push({path: '/'})
        })
        .catch(error => {
          console.log(error)
          console.log('error')
        })
      }
    }
  }
</script>

<style scoped>
  .logo {
      text-align: center;
      padding-top: 60px;
    }

    .center {
      text-align: center;
    }

    .brand {
      color: rgb(120, 120, 120);
      margin: 45px 0;
      font-size: 26px;
    }

    .card {
      background: #fff;
      padding: 45px 22px;
      box-shadow: 0 1px 4px rgba(0,0,0,.04);
      border: 1px solid rgba(0,0,0,.09);
    }

    .login {
      display: flex;
      flex-direction: column;
      width: 300px;
      margin: 0 auto;
    }

    input[type="text"], input[type="password"] {
      padding: 10px 16px;
      font-size: 20px;
      margin-bottom: 22px;
      border: 1px solid #aaa;
      border-radius: 2px;
    }

    .save, .btn {
      margin-bottom: 22px;
    }

    .forget {
      line-height: 24px;
      font-size: 16px;
    }

    .forget a, .signin a {
      color: #95c8ea;
      font-size: 16px;
      text-decoration: underline;
    }

    .save label {
      color: #888;
      padding-left: 10px;
      cursor: pointer;
    }

    .btn {
      height: 45px;
      line-height: 45px;
      font-size: 20px;
      background: rgb(95, 158, 201);
      color: #fff;
      cursor: pointer;
    }
</style>


